import { animate, style, transition, trigger } from '@angular/animations';
import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { StatusDTO } from 'src/app/shared/Models/Models';
import { StepCompletionStatusComponent } from 'src/app/shared/step-completion-status/step-completion-status.component';
import { SearchAccountService } from './search-account.service';

import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { Router } from '@angular/router';


@Component({
  selector: 'app-search-account',
  templateUrl: './search-account.component.html',
  styleUrls: ['./search-account.component.scss'],
  animations: [
    trigger('panelInOut', [
      transition('void => *', [
        style({ transform: 'translateY(-05%)' }),
        animate(500)
      ]),
      transition('* => void', [
        animate(500, style({ transform: 'translateY(-05%)' }))
      ])
    ])
  ]
})
export class SearchAccountComponent implements OnInit, AfterViewInit {
  panelVisible: boolean = true;

  searchForm: FormGroup;
  searchResults: [] = [];

  displayedColumns: string[] = ['client_acct_id', 'acct_no', 'first_name', 'last_name',  'postal_cd', 'city', 'action'];
  totalData: any;
  dataSource: any;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatPaginator) paginator: MatPaginator;


  constructor(private dialog: MatDialog, 
    private searchAccountService: SearchAccountService,
    private router: Router) {
  }


  ngAfterViewInit() {
    this.callPagerAndSort();
  }

  ngOnInit(): void {
    this.callPagerAndSort();
    this.initForm();
  }



  callPagerAndSort() {
    this.dataSource = new MatTableDataSource<SearchAccount>(this.searchResults);
    if (this.dataSource) {
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  private initForm() {
    this.searchForm = new FormGroup({
      'first_name': new FormControl(""),
      'last_name': new FormControl(""),
      'client_acct_id': new FormControl(""),
      'user_id': new FormControl(""),
      'acct_no': new FormControl(""),
      'postal_cd': new FormControl(""),
    });
  }

  onSubmit() {
    const searchModel = this.searchForm.value;

    if (this.searchForm.valid) {
      this.searchAccountService.SearchResults(searchModel).subscribe((result) => {
        if (result && result.error_msg === "OK" && result.error_code === 0) {
          this.searchResults = result.account_details_m;
          if (this.searchResults.length > 0) {
              this.callPagerAndSort();
          }
        }
        else {
          let statusData: StatusDTO = {
            title: 'Unexpected Error occured while seaching.!',
            details: result && result.error_msg.length > 0 ? result.error_msg : "Some error occured, please try again.",
            stepSuccessful: false
          }

          let ref = this.dialog.open(StepCompletionStatusComponent,
            { height: '48vh', width: '42vw', data: statusData });
        }
      });
    }
    else {
      return;
    }

  }

  checkFormValidation() {
    const searchModel = this.searchForm.value;
    if (searchModel.first_name === '' && searchModel.last_name === '' && searchModel.client_acct_id === ''
      && searchModel.user_id === '' && searchModel.acct_no === '' && searchModel.postal_cd === '') {
      return true;
    }
    return false;
  }

  togglePangel() {
    this.panelVisible = !this.panelVisible;
  }

  onAccountViewClick(user:SearchAccount){
    debugger;
    localStorage.setItem("user_id", user.user_id);
    localStorage.setItem("session_id", user.user_id);
    localStorage.setItem("client_acct_id", user.client_acct_id);

    localStorage.setItem("impersonate", 'yes'); // we have to clear this at some point

    localStorage.setItem("first_name", user.first_name);
    localStorage.setItem("last_name", user.last_name);
    localStorage.setItem("email", user.user_id);

    this.router.navigate(["/Login"]);
  }
}

export interface SearchAccount {
  first_name: string;
  last_name: string;
  acct_no: string;
  client_acct_id: string;
  postal_cd: string;
  user_id: string;
}